.demo {
  padding: 15px;
}

h1 {
  color: #1976d2;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
}

.demoContainer {
  display: grid;
  grid-template-rows: 300px 300px 300px;
  grid-template-columns: 400px 400px;
  gap: 20px;
  justify-content: center;
}

.demoContainer_item {
  display: block;
  width: 400px;
  height: 300px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: ease 0.3s;
  -webkit-transition: ease 0.3s;
  -moz-transition: ease 0.3s;
  -ms-transition: ease 0.3s;
  -o-transition: ease 0.3s;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
}

.activeb {
  background-image: url(./images/activeb.jpg);
}

.fashion {
  background-image: url(./images/fashion.jpg);
}

.incdy {
  background-image: url(./images/incdy.jpg);
}

.mogo {
  background-image: url(./images/mogo.jpg);
}

.surf {
  background-image: url(./images/surf.jpg);
}

.demoContainer_item:hover {
  -webkit-box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.75);
}

.demoContainer_item__a {
  grid-column: 1/3;
  justify-self: center;
}

@media only screen and (max-width: 1080px) {
  .demoContainer {
    grid-template-rows: 210px 210px 210px;
    grid-template-columns: 280px 280px;
  }
  .demoContainer_item {
    width: 280px;
    height: 210px;
  }
}

@media only screen and (max-width: 768px) {
  .demoContainer {
    grid-template-rows: 150px 150px 150px;
    grid-template-columns: 200px 200px;
  }
  .demoContainer_item {
    width: 200px;
    height: 150px;
  }
}

@media only screen and (max-width: 568px) {
  .demoContainer {
    grid-template-rows: 210px 210px 210px 210px 210px;
    grid-template-columns: 280px;
  }
  .demoContainer_item__a {
    grid-column: 1;
    justify-self: center;
  }
  .demoContainer_item {
    width: 280px;
    height: 210px;
  }
}
