.about {
  padding: 15px;
  font-size: $fsl;
  color: $clrtext;
  line-height: 2.8rem;
  text-align: justify;
  position: relative;
  .about-content {
    h1 {
      text-align: center;
    }
    a {
      color: #29295e;
      text-decoration: none;
      font-size: $fsl;
      font-weight: $fw;
      &:hover {
        color: $clrbackhov;
      }
    }
  }
  .about__lang {
    position: absolute;
    right: 5px;
    top: 10px;
    font-weight: 600;
    span {
      margin-right: 15px;
      cursor: pointer;
      color: #29295e;
      &:hover {
        color: $clrbackhov;
      }
    }
  }
}

.btn-ant-modal {
  margin: 40px auto;
  margin-bottom: 0;
  text-align: center;
}
.my-modal__btn {
  display: inline-block;
}
.my-modal {
  margin-top: 15px;
  .circle {
    border-radius: 32px;
    -webkit-border-radius: 35%;
    -moz-border-radius: 32px;
    -ms-border-radius: 32px;
    -o-border-radius: 32px;
    -webkit-border-radius: 32px;
  }
}

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  z-index: 2000;
  background-color: rgba(163, 163, 163, 0.5);

  .mod-cont {
    width: 300px;
    height: 200px;
    margin: 300px auto;
    background-color: steelblue;
    animation: mod 0.2s ease-in-out;
    -webkit-animation: mod 0.2s ease-in-out;
  }
}

@media only screen and (max-width: 768px) {
  .about-content {
    h1 {
      font-size: 1.6rem;
    }
    a {
      font-size: 1.4rem;
    }
    font-size: 1.4rem;
  }
}

@media only screen and (max-width: 568px) {
  .about-content {
    h1 {
      font-size: 1.2rem;
    }
    & a {
      font-size: 1.3rem;
    }
    font-size: 1.3rem;
  }
  .about__lang {
    display: flex;
    flex-direction: column;
    top: 5px;
    span {
      font-size: 1.3rem;
      line-height: 2.2rem;
    }
  }
}

@keyframes mod {
  from {
    width: 0px;
    height: 0px;
    margin-top: 0;
  }
  to {
    width: 300px;
    height: 200px;
    margin-top: 300px;
  }
}
